<template>
<div>
  <div id="uploader" class="wu-example">
    <div class="btns" style="float:left;padding-right: 20px">
      <div id="picker"></div>
    </div>
    <div id="ctlBtn" class="webuploader-pick" @click="upload()"></div>

  </div>
  <!---->
  <div id="thelist" class="uploader-list" ></div>
</div>
</template>
<script>
  import $ from '../../../../static/plugins/jquery/dist/jquery.js'
  import webuploader from '../../../../static/plugins/webuploader/dist/webuploader.js'
  import '../../../../static/css/webuploader/webuploader.css'
  export default{
    data(){
      return{
        uploader:{},
        uploadFile:{},
        fileMd5:''
      }
    },
    methods:{
      //
      upload(){
        if(this.uploadFile && this.uploadFile.id){
          this.uploader.upload(this.uploadFile.id);
        }else{
          alert("");
        }
      }
    },
    mounted(){
//      var fileMd5;
//      var uploadFile;
      WebUploader.Uploader.register({
          "before-send-file":"beforeSendFile",
          "before-send":"beforeSend",
          "after-send-file":"afterSendFile"
        },{
          beforeSendFile:function(file) {
            // deffered,
            var deferred = WebUploader.Deferred();
            // 
            (new WebUploader.Uploader()).md5File(file, 0, 100*1024*1024)
              .then(function(val) {

                this.fileMd5 = val;
                this.uploadFile = file;
                //alert(fileMd5 )
                //
                $.ajax(
                  {
                    type:"POST",
                    url:"/api/media/upload/register",
                    data:{
                      // 
                      fileMd5:this.fileMd5,
                      fileName: file.name,
                      fileSize:file.size,
                      mimetype:file.type,
                      fileExt:file.ext
                    },
                    dataType:"json",
                    success:function(response) {
                      if(response.success) {
                        //alert('');
                        deferred.resolve();
                      } else {
                        alert('');
                        deferred.reject();
                      }
                    }
                  }
                );
              }.bind(this));

            return deferred.promise();
          }.bind(this),
          beforeSend:function(block) {
            var deferred = WebUploader.Deferred();
            // 
            $.ajax(
              {
                type:"POST",
                url:"/api/media/upload/checkchunk",
                data:{
                  // 
                  fileMd5:this.fileMd5,
                  // 
                  chunk:block.chunk,
                  // 
                  chunkSize:block.end-block.start
                },
                dataType:"json",
                success:function(response) {
                  if(response.ifExist) {
                    // 
                    deferred.reject();
                  } else {
                    // 
                    deferred.resolve();
                  }
                }
              }
            );
            //fileMd5fileMd5
            this.uploader.options.formData.fileMd5 = this.fileMd5;
            this.uploader.options.formData.chunk = block.chunk;
            return deferred.promise();
          }.bind(this),
          afterSendFile:function(file) {
            // 
            $.ajax(
              {
                type:"POST",
                url:"/api/media/upload/mergechunks",
                data:{
                  fileMd5:this.fileMd5,
                  fileName: file.name,
                  fileSize:file.size,
                  mimetype:file.type,
                  fileExt:file.ext
                },
                success:function(response){

                }
              }
            );
          }.bind(this)
        }
      );
      // uploader
      this.uploader = WebUploader.create(
        {
          swf:"/static/plugins/webuploader/dist/Uploader.swf",//flashh5flash
          server:"/api/media/upload/uploadchunk",//
          fileVal:"file",//name
          pick:"#picker",//
          auto:false,//
          disableGlobalDnd:true,//
          chunked:true,// 
          chunkSize:1*1024*1024, // 5M
          threads:3, // 3
          prepareNextFile:true// 
        }
      );

      // 
      this.uploader.on("fileQueued", function(file) {
          this.uploadFile = file;
          // 
          $("#thelist").append("<div id='" + file.id + "'>" +
            "<span>" + file.name + "</span>" +
            "&nbsp;<span class='percentage'>0%</span>" +
            "</div>");

        }.bind(this)
      );
      //
      this.uploader.on("beforeFileQueued", function(file) {
        var files = this.uploader.getFiles();
        if(files ){
          if(files.length>0){
            alert("");
            return false;
          }
        }
      }.bind(this));

      // 
      // percentage:
      this.uploader.on("uploadProgress", function(file, percentage) {
        $("#" + file.id).find("span.percentage").text(Math.round(percentage * 100) + "%");
      });
      //
      this.uploader.on("uploadError", function(file,reason) {
        console.log(reason)
        alert("");
      });
      //
      this.uploader.on("uploadSuccess", function(file,response ) {
        console.log(response)
        alert("");
      });

    }
  }

</script>
<style scoped>


</style>



