<template>
  <div>
    <!---->
    <el-form :model="params">
      
      <el-input v-model="params.tag" style="width:160px"></el-input>
      
      <el-input v-model="params.fileOriginalName" style="width:160px"></el-input>
      
      <el-select v-model="params.processStatus" placeholder="">
        <el-option
          v-for="item in processStatusList"
          :key="item.id"
          :label="item.name"
          :value="item.id">
        </el-option>
      </el-select>
      <br/>
      <el-button type="primary" v-on:click="query" size="small"></el-button>
      <router-link class="mui-tab-item" :to="{path:'/media/upload'}">
        <el-button  type="primary" size="small" v-if="ischoose != true"></el-button>
      </router-link>
    </el-form>
    <!---->
    <el-table :data="list" highlight-current-row v-loading="listLoading" style="width: 100%;">
      <el-table-column type="index" width="30">
      </el-table-column>
      <el-table-column prop="fileOriginalName" label="" width="220">
      </el-table-column>
      <el-table-column prop="fileName" label="" width="220">
      </el-table-column>
      <el-table-column prop="fileUrl" label="url" width="260">
      </el-table-column>
      <el-table-column prop="tag" label="" width="100">
      </el-table-column>
      <el-table-column prop="fileSize" label="" width="120">
      </el-table-column>
      <el-table-column prop="processStatus" label="" width="100" :formatter="formatProcessStatus">
      </el-table-column>
      <el-table-column prop="uploadTime" label="" width="110" :formatter="formatCreatetime">
      </el-table-column>
      <el-table-column label="" width="100" v-if="ischoose != true">
        <template slot-scope="scope">
          <el-button
            size="small" type="primary" plain @click="process(scope.row.fileId)">
          </el-button>
        </template>
      </el-table-column>
      <el-table-column label="" width="80" v-if="ischoose == true">
        <template slot-scope="scope">
        <el-button
          size="small" type="primary" plain @click="choose(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-col :span="24" class="toolbar">

      <el-pagination background layout="prev, pager, next" @current-change="changePage" :page-size="this.params.size"
                     :total="total" :current-page="this.params.page"
                     style="float:right;">
      </el-pagination>
    </el-col>
  </div>
</template>
<script>
  import * as mediaApi from '../api/media'
  import utilApi from '@/common/utils';
  export default{
    props: ['ischoose'],
    data(){
      return {
        params:{
          page:1,//
          size:10,//
          tag:'',//
          fileName:'',//
          processStatus:''//
        },
        listLoading:false,
        list:[],
        total:0,
        processStatusList:[]
      }
    },
    methods:{
      formatCreatetime(row, column){
        var createTime = new Date(row.uploadTime);
        if (createTime) {
          return utilApi.formatDate(createTime, 'yyyy-MM-dd hh:mm:ss');
        }
      },
      formatProcessStatus(row,column){
        var processStatus = row.processStatus;
        if (processStatus) {
            if(processStatus == '303001'){
              return "";
            }else if(processStatus == '303002'){
              return "";
            }else if(processStatus == '303003'){
              return "";
            }else if(processStatus == '303004'){
              return "";
            }
        }
      },
      choose(mediaFile){
          if(mediaFile.processStatus !='303002' && mediaFile.processStatus !='303004'){
            this.$message.error('');
            return ;
          }
        if(!mediaFile.fileUrl){
          this.$message.error('url');
          return ;
        }
        //choosemedia
        this.$emit('choosemedia',mediaFile.fileId,mediaFile.fileOriginalName,mediaFile.fileUrl);
      },
      changePage(page){
        this.params.page = page;
        this.query()
      },
      process (id) {
//        console.log(id)
        mediaApi.media_process(id).then((res)=>{
          console.log(res)
         if(res.success){
           this.$message.success('');
         }else{
           this.$message.error('');
         }
        })
      },
      query(){
        mediaApi.media_list(this.params.page,this.params.size,this.params).then((res)=>{
          console.log(res)
          this.total = res.queryResult.total
          this.list = res.queryResult.list
        })
      }
    },
    created(){
        //
      this.params.page = Number.parseInt(this.$route.query.page||1);
    },
    mounted() {
      //
      this.query()
      //
      this.processStatusList = [
        {
          id:'',
          name:''
        },
        {
          id:'303001',
          name:''
        },
        {
          id:'303002',
          name:''
        },
        {
          id:'303003',
          name:''
        },
        {
          id:'303004',
          name:''
        }
      ]
    }
  }
</script>
<style>

</style>



