<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div class="text item">
        <el-button type="primary"  @click.native="preview" ></el-button>
        <br/><br/>
        <span v-if="previewurl && previewurl!=''"><a :href="previewurl" target="_blank"> </a> </span>
      </div>
    </el-card><br/>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div class="text item">
        <div v-if="course.status == '202001'">
          <br/>
          <el-button type="primary"  @click.native="publish" ></el-button>
        </div>
        <div v-else-if="course.status == '202003'">
          
          <br/><br/>
          <span><a :href="'http://www.xuecheng.com/course/detail/'+this.courseid+'.html'" target="_blank"> </a> </span>
        </div>
        <div v-else-if="course.status == '202002'">
          <br/>
          <el-button type="primary"  @click.native="publish" ></el-button>
          <br/><br/>
          <span><a :href="'http://www.xuecheng.com/course/detail/'+this.courseid+'.html'" target="_blank"> </a> </span>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script>
  import * as courseApi from '../../api/course';
  import utilApi from '../../../../common/utils';
  export default {
    data() {
      return {
        dotype:'',
        courseid:'',
        course: {"id":"","name":"","status":""},
        previewurl:''
      }
    },
    methods: {
      //
      preview(){
        courseApi.preview(this.courseid).then((res) => {
          if(res.success){
            this.$message.error('');
            if(res.url){
              //url
              this.previewurl = res.url
            }
          }else{
            this.$message.error('');
          }

        });
      },
      //
      publish(){
        this.$confirm('', '', {}).then(() => {
          courseApi.publish(this.courseid).then((res) => {
            if(res.success){
              this.$message.success('');
            }else if(res.message){
              this.$message.error(res.message);
            }else{
              this.$message.error("");
            }

          });
        });

      }
    },
    mounted(){
      this.dotype = this.$route.params.dotype;
      //id
      this.courseid = this.$route.params.courseid;
      console.log("courseid=" + this.courseid)
      //
      courseApi.getCoursebaseById(this.courseid).then((res) => {
        console.log(res);
        this.course = res;

      });
    }
  }
</script>
<style scoped>


</style>



